import { message } from "antd"
import axios from "axios"

// const API_HOST = "//" + process.env.ADMIN_API_HOST

let token = "asdffdfdf"
let putUrl = "https://subscribe-and-contact-form.p.rapidapi.com/v1/contacts"
let putHeaders = {
  "content-type": "application/json",
  "x-rapidapi-host": "subscribe-and-contact-form.p.rapidapi.com",
  "x-rapidapi-key": "024726ac3bmsh58ecf30df1dcaa9p187c38jsnae3868b21908",
  "useQueryString": true
}
// let putUrl = 'http://localhost:1080/v1/contacts'
// let putHeaders = { "content-type": "application/json", };
let putContactData = function (contactData, captcha_response = "any_string") {
  return new Promise(function (resolve) {
    // request
    axios({
      method: "put",
      url: putUrl,
      headers: putHeaders,
      data: {
        contact: contactData,
        captcha_response: captcha_response
      }
    })
      .then((results) => {
        // parse data from response
        // output data, NOT object with property "data"
        if (results.data && "data" in results.data) {
          resolve(results.data.data)
        } else {
          console.warn('server response did not have "data" key')
          resolve(results.data)
        }
      })
      .catch((error) => {
        // parse error text from response
        // output object with property "error"
        let data = error.response ? error.response.data : { error: "" }
        data.error = data.error || error.message ? error.message : error
        console.log("axios error", data)
        resolve(data)
      })
  })
}

/*
 * Submit contact form
 */
export function RX__contact(contactData) {
  return (dispatch) => {
    if (!"updates_product" in contactData) {
      contactData.updates_product = true
    }
    if (!contactData.email) {
      message.error(`Please enter your email address.`, 10)
      return
    }
    // grecaptcha.ready(function () {
    //   grecaptcha.execute("6LfSN-MUAAAAAOxMUojSlBxkicjSeX1YLW8ds8C1", { action: "submit" }).then(function (token) {
    console.log("captcha_response", token)
    if (typeof window==='object') window.isLoading("contact")
    putContactData(contactData, token).then(function (data) {
      if (typeof window==='object') window.doneLoading("contact")
      if (data.error) {
        message.error(data.error || "Could not send. Something went wrong.", 10)
      } else {
        if (contactData.text) {
          message.success("Message sent. Thank you. We'll get back to you soon.", 10)
        } else {
          message.success("Email saved. Thanks. We promise not to spam you!", 10)
        }
      }
    })
    //   })
    // })
  }
}
